/**
 * 反转promise的异步控制的对象模型——Deferred
 * @returns
 */
class Deferred {
    constructor() {
        /**
         * promise是否已经完成
         * @type {boolean}
         */
        this.$isFinished = false;
        //返回的参数
        this.promise = new Promise((resolve, reject) => {
            this.$resolve = resolve;
            this.$reject = reject;
        });
    }
    get isFinished() {
        return this.$isFinished;
    }
    /**
     * 对外暴露的接口，promise正常完成
     * @param {T} args
     */
    resolve(args) {
        this.$isFinished = true;
        this.$resolve(args);
    }
    /**
     * 对外暴露的接口，promise异常结束
     * @param {any} args
     */
    reject(args) {
        this.$isFinished = true;
        this.$reject(args);
    }
}
exports.Deferred = Deferred;



